
import DropBox from "./DropBox";
import Bullet from "./Bullet";
/**
 * æ¸¸æˆæ§åˆ¶è„šæœ¬ã€‚å®šä¹‰äº†å‡ ä¸ªdropBoxï¼Œbulletï¼ŒcreateBoxIntervalç­‰å˜é‡ï¼Œèƒ½å¤Ÿåœ¨IDEæ˜¾ç¤ºåŠè®¾ç½®è¯¥å˜é‡
 * æ›´å¤šç±»å‹å®šä¹‰ï¼Œè¯·å‚è€ƒå®˜æ–¹æ–‡æ¡£
 */
export default class GameControl extends Laya.Script {
    /** @prop {name:dropBox,tips:"æ‰è½å®¹å™¨é¢„åˆ¶ä½“å¯¹è±¡",type:Prefab}*/
    dropBox: Laya.Prefab;
    /** @prop {name:bullet,tips:"å­å¼¹é¢„åˆ¶ä½“å¯¹è±¡",type:Prefab}*/
    bullet: Laya.Prefab;
    /** @prop {name:createBoxInterval,tips:"é—´éš”å¤šå°‘æ¯«ç§’åˆ›å»ºä¸€ä¸ªä¸‹è·Œçš„å®¹å™¨",type:int,default:1000}*/
    createBoxInterval: number = 1000;
    /**å¼€å§‹æ—¶é—´*/
    private _time: number = 0;
    /**æ˜¯å¦å·²ç»å¼€å§‹æ¸¸æˆ */
    private _started: boolean = false;
    /**å­å¼¹å’Œç›’å­æ‰€åœ¨çš„å®¹å™¨å¯¹è±¡ */
    private _gameBox: Laya.Sprite;

    constructor() { super(); }

    onEnable(): void {
        this._time = Date.now();
        this._gameBox = this.owner.getChildByName("gameBox") as Laya.Sprite;
    }

    onUpdate(): void {
        //æ¯é—´éš”ä¸€æ®µæ—¶é—´åˆ›å»ºä¸€ä¸ªç›’å­
        let now = Date.now();
        if (now - this._time > this.createBoxInterval&&this._started) {
            this._time = now;
            this.createBox();
        }
    }

    createBox(): void {
        //ä½¿ç”¨å¯¹è±¡æ± åˆ›å»ºç›’å­
        let box: Laya.Sprite = Laya.Pool.getItemByCreateFun("dropBox", this.dropBox.create, this.dropBox);
        box.pos(Math.random() * (Laya.stage.width - 100), -100);
        this._gameBox.addChild(box);
    }

    onStageClick(e: Laya.Event): void {
        //åœæ­¢äº‹ä»¶å†’æ³¡ï¼Œæé«˜æ€§èƒ½ï¼Œå½“ç„¶ä¹Ÿå¯ä»¥ä¸è¦
        e.stopPropagation();
        //èˆå°è¢«ç‚¹å‡»åï¼Œä½¿ç”¨å¯¹è±¡æ± åˆ›å»ºå­å¼¹
        let flyer: Laya.Sprite = Laya.Pool.getItemByCreateFun("bullet", this.bullet.create, this.bullet);
        flyer.pos(Laya.stage.mouseX, Laya.stage.mouseY);
        this._gameBox.addChild(flyer);
    }

    /**å¼€å§‹æ¸¸æˆï¼Œé€šè¿‡æ¿€æ´»æœ¬è„šæœ¬æ–¹å¼å¼€å§‹æ¸¸æˆ*/
    startGame(): void {
        if (!this._started) {
            this._started = true;
            this.enabled = true;
        }
    }

    /**ç»“æŸæ¸¸æˆï¼Œé€šè¿‡éæ¿€æ´»æœ¬è„šæœ¬åœæ­¢æ¸¸æˆ */
    stopGame(): void {
        this._started = false;
        this.enabled = false;
        this.createBoxInterval = 1000;
        this._gameBox.removeChildren();
    }
}