import GameUI from "./GameUI";
/**
 * æ‰è½ç›’å­è„šæœ¬ï¼Œå®ç°ç›’å­ç¢°æ’åŠå›æ”¶æµç¨‹
 */
export default class DropBox extends Laya.Script {
    /**ç›’å­ç­‰çº§ */
    level: number = 1;
    /**ç­‰çº§æ–‡æœ¬å¯¹è±¡å¼•ç”¨ */
    private _text: Laya.Text;
    /**åˆšä½“å¯¹è±¡å¼•ç”¨ */
    private _rig: Laya.RigidBody

    constructor() { super(); }
    onEnable(): void {
        /**è·å¾—ç»„ä»¶å¼•ç”¨ï¼Œé¿å…æ¯æ¬¡è·å–ç»„ä»¶å¸¦æ¥ä¸å¿…è¦çš„æŸ¥è¯¢å¼€é”€ */
        this._rig = this.owner.getComponent(Laya.RigidBody);
        this.level = Math.round(Math.random() * 5) + 1;
        this._text = this.owner.getChildByName("levelTxt") as Laya.Text;
        this._text.text = this.level + "";
    }

    onUpdate(): void {
        //è®©æŒç»­ç›’å­æ—‹è½¬
        (this.owner as Laya.Sprite).rotation++;
    }

    onTriggerEnter(other: any, self: any, contact: any): void {
        var owner: Laya.Sprite = this.owner as Laya.Sprite;
        if (other.label === "buttle") {
            //ç¢°æ’åˆ°å­å¼¹åï¼Œå¢åŠ ç§¯åˆ†ï¼Œæ’­æ”¾å£°éŸ³ç‰¹æ•ˆ
            if (this.level > 1) {
                this.level--;
                this._text.changeText(this.level + "");
                owner.getComponent(Laya.RigidBody).setVelocity({ x: 0, y: -10 });
                Laya.SoundManager.playSound("sound/hit.wav");
            } else {
                if (owner.parent) {
                    let effect: Laya.Animation = Laya.Pool.getItemByCreateFun("effect", this.createEffect, this);
                    effect.pos(owner.x, owner.y);
                    owner.parent.addChild(effect);
                    effect.play(0, true);
                    owner.removeSelf();
                    Laya.SoundManager.playSound("sound/destroy.wav");
                }
            }
            GameUI.instance.addScore(1);
        } else if (other.label === "ground") {
            //åªè¦æœ‰ä¸€ä¸ªç›’å­ç¢°åˆ°åœ°æ¿ï¼Œåˆ™åœæ­¢æ¸¸æˆ
            owner.removeSelf();
            GameUI.instance.stopGame();
        }
    }

    /**ä½¿ç”¨å¯¹è±¡æ± åˆ›å»ºçˆ†ç‚¸åŠ¨ç”» */
    createEffect(): Laya.Animation {
        let ani: Laya.Animation = new Laya.Animation();
        ani.loadAnimation("test/TestAni.ani");
        ani.on(Laya.Event.COMPLETE, null, recover);
        function recover(): void {
            ani.removeSelf();
            Laya.Pool.recover("effect", ani);
        }
        return ani;
    }

    onDisable(): void {
        //ç›’å­è¢«ç§»é™¤æ—¶ï¼Œå›æ”¶ç›’å­åˆ°å¯¹è±¡æ± ï¼Œæ–¹ä¾¿ä¸‹æ¬¡å¤ç”¨ï¼Œå‡å°‘å¯¹è±¡åˆ›å»ºå¼€é”€ã€‚
        Laya.Pool.recover("dropBox", this.owner);
    }
}